import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpCartaoRespostaLista } from '../../cartao-resposta-lista/cartao-resposta-lista';
import { TpCartaoRespostaItem } from '../cartao-resposta-item';
import { TpCartaoRespostaItemType } from '../utils/cartao-resposta-item.type';

const meta: Meta<
  TpCartaoRespostaItem & TpCartaoRespostaLista & TpCartaoRespostaItemType
> = {
  title: 'components/Core/Cartão resposta item',
  argTypes: {
    canceled: {
      control: { type: 'boolean' },
      description: 'Define a estilização de cor do componente (line-through).',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
    printed: {
      control: { type: 'boolean' },
      description: 'Define a estilização de cor do componente (background).',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
    active: {
      control: { type: 'boolean' },
      description:
        'Define a estilização de cor do componente (background com gradient).',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
    incorrect: {
      control: { type: 'boolean' },
      description: 'Define a estilização de cor do componente (color warning).',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
    correct: {
      control: { type: 'boolean' },
      description: 'Define a estilização de cor do componente (color success).',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
    discursive: {
      control: { type: 'boolean' },
      description: 'Define a estilização de cor do componente (color brand).',
      table: {
        defaultValue: { summary: false },
        type: { summary: 'boolean' },
      },
    },
    slot: {
      control: { type: 'text' },
      description: 'Define o texto do componente.',
      table: {
        defaultValue: {
          summary: '',
        },
        type: {
          summary: 'string',
        },
      },
    },
  },
  args: {
    slot: '01',
  },
};

export default meta;

type Story = StoryObj<
  TpCartaoRespostaItem & TpCartaoRespostaLista & TpCartaoRespostaItemType
>;

export const Default: Story = {
  render: ({ ...args }) => {
    return html`
      <tp-cartao-resposta-lista>
        <tp-cartao-resposta-item
          color=${args.color}
          ?active=${args.active}
          ?printed=${args.printed}
          ?canceled=${args.canceled}
          ?incorrect=${args.incorrect}
          ?correct=${args.correct}
          ?discursive=${args.discursive}
        >
          ${args.slot}
        </tp-cartao-resposta-item>
      </tp-cartao-resposta-lista>
    `;
  },
};
